import { defineComponent } from 'vue';
export default defineComponent({
    props: {
        items: {
            type: Array,
            default: []
        }
    },
    emits: {
        change: (index) => true
    },
    data() {
        return {
            tabIndex: 0,
        };
    },
    methods: {
        selectTab(index) {
            this.tabIndex = index;
            this.$emit("change", index);
        }
    }
});
