import { defineComponent } from 'vue';
export default defineComponent({
    props: {
        type: {
            type: String,
            default: "text"
        },
        icon: {
            type: String,
            default: "fa-solid fa-search"
        },
        placeholder: {
            type: String,
            default: "Search"
        }
    },
    emits: {
        "change": (value) => true,
    },
    data() {
        return {
            value: null,
            modalSettings: {
                width: 700,
                height: 500,
                backgroundColor: "2B2B2B"
            }
        };
    },
    methods: {
        changeValue() {
            this.$emit("change", this.value);
        }
    }
});
